// Helper functions for status colors
const getStatusClasses = (status) => {
	if (status === 'danger') return { text: 'text-red-400', bar: 'bg-red-600' };
	if (status === 'warning') return { text: 'text-yellow-400', bar: 'bg-yellow-500' };
	return { text: 'text-emerald-400', bar: 'bg-emerald-500' };
};

// Reusable Metric Card Component
const MetricCard = ({ title, value, subtext, status, icon: Icon, trend, trendValue = 0 }) => {
	const { text: statusTextColor, bar: statusBarColor } = getStatusClasses(status);
	
	let barWidth = '70%'; 
	
	if (title.includes('Fatigue')) {
		const rpeMax = Math.max(...(value.toString().split(' | ').map(v => parseFloat(v)) || [0]));
		barWidth = `${Math.min(rpeMax * 10, 100)}%`;
	} else if (title.includes('Recovery')) {
		const sleepValue = parseFloat(value.toString().replace('h', ''));
		barWidth = `${Math.min((sleepValue / 8) * 100, 100)}%`; // Based on 8 hours ideal
	} else if (title.includes('Load')) {
		const acRatio = parseFloat(value);
		barWidth = `${Math.min(acRatio * 70, 100)}%`; // Visual scale for A:C Ratio, cap at 100%
	} else if (title.includes('Nutrition')) {
		const adherence = parseFloat(value);
		barWidth = `${adherence}%`; 
	}

	const trendDisplay = trend === 'up' 
		? `+${trendValue.toFixed(1)}%` 
		: trend === 'down' 
		? `-${trendValue.toFixed(1)}%` 
		: '0%';

	return (
		<div className="bg-zinc-900 border border-zinc-800 p-5 rounded-xl relative overflow-hidden group transition-all duration-300 hover:shadow-lg hover:shadow-yellow-500/10">
			<div className="flex justify-between items-start mb-4">
				<div className="p-2 bg-zinc-800 rounded-lg text-yellow-500 group-hover:text-yellow-400 group-hover:bg-zinc-700 transition-colors">
					<Icon size={20} />
				</div>
				
				{trend && (
					<div className="flex items-center text-xs font-bold transition-colors">
						{trend === 'up' ? 
							<span className='text-green-500 flex items-center'><ArrowUpRight size={14} /></span> : 
							trend === 'down' ? 
							<span className='text-red-500 flex items-center'><ArrowDownRight size={14} /></span> : 
							<span className='text-zinc-500 flex items-center'><Minus size={14} /></span>
						}
						<span className={`ml-1 hidden sm:inline-block ${trend === 'up' ? 'text-green-500' : trend === 'down' ? 'text-red-500' : 'text-zinc-500'}`}>{trendDisplay}</span>
					</div>
				)}
			</div>
			
			<h3 className="text-zinc-400 text-xs font-bold uppercase tracking-widest mb-1">{title}</h3>
			<div className="flex items-baseline gap-2">
				<span className="text-3xl font-extrabold text-white">{value}</span>
				<span className={`text-sm font-medium ${statusTextColor}`}>{subtext}</span>
			</div>

			{/* Visual Indicator Bar */}
			<div className="w-full h-1.5 bg-zinc-800 mt-4 rounded-full overflow-hidden">
				<div className={`h-full rounded-full transition-all duration-700 ${statusBarColor}`} style={{ width: barWidth }}></div>
			</div>
		</div>
	);
};

