// Reusable Radial Progress Component
const RadialProgress = ({ score }) => {
	const radius = 50;
	const stroke = 10;
	const normalizedRadius = radius - stroke / 2;
	const circumference = normalizedRadius * 2 * Math.PI;
	const strokeDashoffset = circumference - (score / 100) * circumference;

	let color = "stroke-emerald-400";
	if (score < 85) color = "stroke-yellow-500";
	if (score < 70) color = "stroke-red-500";

	return (
		<div className="relative flex items-center justify-center w-36 h-36">
			<svg height={radius * 2} width={radius * 2}>
				<circle
					className="stroke-zinc-800"
					strokeWidth={stroke}
					fill="transparent"
					r={normalizedRadius}
					cx={radius}
					cy={radius}
				/>
				<circle
					className={`${color} transition-all duration-1000 ease-out`}
					strokeWidth={stroke}
					strokeDasharray={circumference + ' ' + circumference}
					style={{ strokeDashoffset }}
					strokeLinecap="round"
					fill="transparent"
					r={normalizedRadius}
					cx={radius}
					cy={radius}
					transform={`rotate(-90 ${radius} ${radius})`} 
				/>
			</svg>
			<div className="absolute text-center">
				<span className="text-4xl font-extrabold text-white block">{score}</span>
				<span className="text-[10px] text-zinc-400 uppercase tracking-widest">Readiness</span>
			</div>
		</div>
	);
};


